.truncate-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.vr-line {
  display: inline-block;
  width: 1px;
  height: 100%;
  background-color: #ddd;
}
.hr-line {
  display: block;
  height: 0.1rem;
  width: 100%;
  background-color: #ddd;
}
.relative-container {
  position: relative;
}
.secondary--text {
  color: $dark-gray;
}
.secondary--muted {
  color: $medium-gray;
}
h6 {
  font-size: 1.4rem;
}

.avoid-navbar {
  margin-top: $navbar-height;
}

.validation-input-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  .not-valid {
    border: 1px solid #f00 !important;
  }
}

/* date picker override */
.DateRangePicker {
  z-index: 99999999;
}
.responsive_img_container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  img {
    display: block;
    object-fit: cover;
    height: 100% !important ;
    width: 100% !important;
  }
}

.flex-centered {
  @extend %flx-cnt;
}
