.home {
  .getStarted {
    height: 80rem;
    position: relative;
    display: flex;
    flex-direction: row;
    @media (max-width: 576px) {
      flex-direction: column;
    }

    &__about {
      color: gray;
      width: 100%;
      margin-top: 4rem;
      @media (max-width: 992px) {
        margin-top: 2rem;
      }
      @media (max-width: 768px) {
        margin-top: 52rem;
        margin-left: 7%;
      }
      @media (max-width: 576px) {
        margin-top: 40rem;
      }

      h2 {
        margin-top: 5rem;
        margin-bottom: 2rem;
        font-weight: bolder;
      }
      p {
        width: 30%;

        @media (max-width: 768px) {
          width: 70%;
        }
      }
    }
    &__images-container {
      display: flex;
    }
    &__curve {
      z-index: -1;
      position: absolute;
      width: 100%;
      height: 102%;
      top: -270px;
      right: -420px;
      // Extra large devices (large desktops, 1200px and up)
      @media (max-width: 1200px) {
        transform: scale(1);
      }

      // Large devices (desktops, 992px and up)
      @media (max-width: 992px) {
        transform: scale(1.6);
      }
      // Medium devices (tablets, 768px and up)
      @media (max-width: 768px) {
        transform: scale(2.5);
        top: -435px;
        right: -250px;
      }
      @media (max-width: 576px) {
        transform: scale(2.3);
        top: -435px;
      }
    }

    &__img {
      width: 45%;
      height: 47%;
      position: absolute;
      right: 20%;
      top: 20%;
      @media (max-width: 992px) {
        width: 40%;
        height: 40%;
        top: 27%;
      }
      // Medium devices (tablets, 768px and up)
      @media (max-width: 768px) {
        right: 42%;
        top: 8%;
        width: 45%;
        height: 45%;
      }
      @media (max-width: 576px) {
        right: 25%;
        top: 8%;
        width: 45%;
        height: 45%;
      }
    }
  }
  .applicant-benefits {
    margin-bottom: 10rem;
    @media (max-width: 992px) {
      margin-left: 5%;
      margin-top: -90px;
    }

    @media (max-width: 768px) {
      margin-top: 17rem;
    }
    @media (max-width: 576px) {
      margin-top: 10rem;
    }
    &__text {
      h2 {
        color: $dark_blue;
        font-weight: bolder;
        margin-bottom: 4rem;
        width: 70%;
      }
      p {
        width: 80%;
      }
    }
    &__img {
      @media (max-width: 992px) {
        margin-top: 5rem;
      }
      img {
        width: 90%;
        height: 100%;
      }
    }
  }
  .companies-benefits {
    width: 100vw;
    min-height: 72.7rem;
    margin: 10rem 0 9rem 0;
    position: relative;

    @media (max-width: 1200px) {
      margin: 10rem 0px 0px 0px;
    }
    @media (max-width: 992px) {
      background-color: #f6f6f6;
      min-height: 37rem;
      margin: 10rem 0 6rem 0;
    }
    @media (max-width: 768px) {
      min-height: 45rem;
    }
    &__img {
      width: 100%;

      @media (max-width: 992px) {
        display: none;
      }
    }
    &__text {
      position: absolute;
      top: 10%;
      width: 48%;

      margin-left: 9%;

      @media (max-width: 992px) {
        margin-left: 20%;
        width: 55%;
        //text-align: center;
      }

      h3 {
        margin-bottom: 4rem;
        color: $dark_blue;
        font-weight: bolder;
        @media (max-width: 992px) {
          margin-bottom: 2rem;
          font-size: 2rem;
        }
      }
      p {
        @media (max-width: 576px) {
          font-size: 1.4rem;
        }
      }
    }
  }
}
