.homeCard {
  text-align: center;
  width: 100%;
  margin-bottom: 9rem;
  padding: 3rem 1rem;
  min-height: 350px;
  cursor: pointer;
  transform: scale(.9);
  transition: all .3s ease;
  border-radius: 5rem;
  @media (max-width: 767.98px) {
    transform: scale(.7);
  }
 &__img{
     width:50%;
 }
  &:hover {
    
    box-shadow: 1px 1px 5px 0px #dddddd, -1px -1px 5px 0px #dddddd;
    transform: scale(1);
    @media (max-width: 767.98px) {
      transform: scale(.9);
    }
    
  }
  h3 {
    color: #cdc446;
    margin: 2rem 0;
  }
  p {
    color: gray;
  }
}
