.company-profile {
  background-color: $light-gray;
  margin-top: $navbar-height;
  .company-profile__header {
    position: relative;
    margin-bottom: 5rem;
    .header__background {
      width: 100%;
      margin-top: 4rem;
    }
    .header__img {
      border-radius: 50%;
      width: 11rem;
      height: 11rem;
      background-color: white;
      position: absolute;
      text-align: center;
      padding-top: 2rem;
      top: 65%;
      left: 5%;
      border: solid 0.05rem rgb(199, 199, 199);
      @media (max-width: 1200px) {
        top: 60%;
      }
      @media (max-width: 992px) {
        top: 61%;
        width: 8rem;
        height: 8rem;
      }
      @media (max-width: 768px) {
        width: 7rem;
        height: 7rem;
      }
      img {
        width: 70%;
        height: 70%;
      }
    }
    .header__text {
      position: absolute;
      top: 77%;
      left: 17%;
      color: white;
      @media (max-width: 1200px) {
        left: 20%;
      }
      @media (max-width: 992px) {
        top: 72%;
      }
      @media (max-width: 768px) {
        top: 65%;
        left: 22%;
      }
    }
  }
  .company-about {
    margin-left: 0;

    min-height: 13rem;
    @media (max-width: 992px) {
      margin: 0;
    }
    &__text {
      background-color: white;
      min-height: 13rem;
      padding: 2rem 2rem 2rem 3rem;
      border: solid 0.05rem $medium-gray;
      border-radius: 1rem;
      color: $darker-gray;
      h4 {
        font-weight: bolder;
      }
    }
    .company-about-social {
      @media (max-width: 992px) {
        padding: 0;
      }

      .company-location {
        text-align: center;
        background-color: white;
        height: 48%;
        border: solid 0.05rem $medium-gray;
        padding: 1.2rem 0 1.5rem 0;
        border-radius: 1rem;
        @media (max-width: 992px) {
          margin-top: 1rem;
        }
        img {
          width: 2rem;
        }
        span {
          font-size: 1.2rem;
        }
      }
      .company-contacts {
        background-color: white;
        height: 48%;
        border: solid 0.05rem $medium-gray;
        padding: 1rem 2rem;
        border-radius: 1rem;
        margin-top: 2%;

        &__icons {
          width: 40%;
          h5 {
            text-align: center;
            color: $darker-gray;
            font-weight: bolder;
            @media (max-width: 992px) {
              text-align: left;
            }
          }
          ul {
            li {
              img {
                width: 2rem;
                margin-right: 1rem;
              }
            }
          }
        }
        button {
          height: 3rem;
          width: 6rem;
          font-weight: bolder;
          font-size: 1em;
          margin-top: 2rem;
          border-radius: 0.5rem;
        }
      }
    }
  }
  .company-profile-content {
    margin-top: 2rem;
    .col-lg-6 {
      padding: 0;
      @media (max-width: 992px) {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
      }
    }
    .write-post {
      display: flex;
      height: 20rem;
      background-color: white;
      border: solid 0.05rem $medium-gray;
      padding: 3rem 2rem;
      border-radius: 1rem;
      @media (max-width: 1200px) {
        height: 30rem;
      }

      .vodafone-icon {
        height: 6rem;
        width: 6rem;
        margin-right: 1rem;
      }
      &__text {
        width: 95%;

        textarea {
          width: 100%;
          height: 75%;
          outline: 0;
          border-radius: 1rem;
          border: solid 0.05rem $medium-gray;
          padding: 1rem 1.5rem;
          margin-bottom: 1rem;
          @media (max-width: 1200px) {
            height: 60%;
          }
        }
        span {
          font-size: 1.1rem;
          margin-left: 0.5rem;
        }
        .upload-video {
          margin-right: 1.8rem;

          cursor: pointer;
          img {
            width: 1.7rem;
          }
          @media (max-width: 1200px) {
            margin-bottom: 1rem;
          }
        }
        .apply-btn {
          margin-right: 2rem;
          @media (max-width: 1200px) {
            margin-bottom: 1rem;
          }
        }
        .job-url {
          width: 33.33%;
          input {
            outline: 0;
            border: solid 0.05rem $medium-gray;
            padding: 0.5rem 1rem;
            border-radius: 1rem;
          }
        }
      }
    }
  }
  .jobs {
    border: solid 0.05rem $medium-gray;
    background-color: white;
    border-radius: 1rem;
    height: 5rem;
    padding-top: 1.5rem;
    font-weight: bolder;
    @media (max-width: 992px) {
      margin-top: 2rem;
    }
  }
}
