.exp-modal {
  .modal-header {
    border: none;
    background-color: $dark-gray;
    color: #fff;
    justify-content: center;
    padding: 0.5rem !important;
    h2 {
      font-size: 2rem;
      text-transform: capitalize;
    }
  }
  .modal-content {
    overflow: visible;
    border-radius: 1rem !important  ;
  }
  .modal-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: visible;
    .exp-input {
      width: 80%;
      height: 3.5rem;
      outline: 0;
      border-radius: 0.5rem;
      padding: 1rem;
      border: solid 0.05rem rgb(199, 199, 199);
      color: $darker_gray;
      margin-bottom: $margin-md;
      &:focus {
      }
    }

    .datalist-input {
      &:first-child {
        margin-right: 0.5rem;
      }
      &:last-child {
        margin-left: 0.5rem;
      }
    }
    .select-container {
      width: 80%;
    }
  }

  .modal-footer {
    button {
      border-radius: 2rem;
      min-width: 10rem;
    }
  }
}
