.postCard {
  background-color: white;
  min-height: 45rem;
  display: flex;
  flex-direction: column;
  border: solid 0.05rem $medium-gray;
  border-radius: 1rem;
  margin-top: 2rem;
  padding: 0rem 3rem 2rem 3rem;
  color: gray;

  &__header {
    margin-bottom: 0rem;

    &__text {
      padding: 3rem 0 0 1.5rem;
    }
  }
  &__img {
    width: 100%;
  }
  p {
    margin: 0rem 0 3rem 0;
    @media (max-width: 992px) {
      margin: 2rem 0 3rem 0;
    }
  }
  button {
    width: 40%;
    height: 3rem;
    margin: 2rem auto 0 auto;
    border-radius: 1rem;
    font-weight: bolder;
    font-size: 1.4rem;
  }
}
